.sh-burger {
  position: relative;
  display: none;
  width: 34px;
  height: 26px;
  cursor: pointer; }
  .sh-burger span,
  .sh-burger span::before,
  .sh-burger span::after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 34px;
    height: 2px;
    margin-top: -1px;
    margin-left: -17px;
    background-color: #fff; }
  .sh-burger span::before,
