  font-size: 16px;
  line-height: 135%;
  transition-duration: .2s;
  max-width: calc(710px - 40px);
  width: 100%; }

.ans-iq-right-indicator {
  position: absolute;
  right: 54px;
  width: 18px;
  height: 9px;
  background: url(../img/answers.svg) no-repeat center center;
  background-size: cover;
  top: 31px;
  transition-duration: .2s; }

.ans-item-answer {
  display: none;
  background: #FFFFFF;
  padding: 25px 32% 22px 96px;
  box-sizing: border-box;
  position: relative;
  font-size: 16px;
  line-height: 135%; }
  .ans-item-answer::before {
    content: '';
    display: block;
    background: #F26419;
    height: 100%;
    width: 11px;
    position: absolute;
    left: 0;
    top: 0; }

.site-footer {
  background: #2C2E85;
  padding-top: 47px; }

.sf-container {
  display: flex;
  justify-content: space-between; }

.sf-col-1 {
  width: 240px; }

.sf-logo {
  width: 123px; }
  .sf-logo img {
    max-width: 100%; }

.sf-copyright {
  font-size: 14px;
  line-height: 150%;
  margin-top: 10px;
  padding-left: 44px;
  color: #9D9EBF; }

.sf-socials-left {
  padding-left: 44px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 49px; }
  .sf-socials-left__item {
    display: block;
    margin-right: 32px;
    margin-bottom: 15px;
    transition-duration: .2s; }
    .sf-socials-left__item img {
      display: block;
      max-width: 29px;
      max-height: 22px;
      width: 100%;
      height: auto; }
    .sf-socials-left__item:hover {
      opacity: .7; }

.sf-col-title {
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  color: #FFFFFF;
  padding-bottom: 17px; }

.sf-col-2 {
  margin-top: 17px;
  margin-left: 48px;
  padding-left: 56px;
  border-left: 1px solid rgba(255, 255, 255, 0.12); }

.sf-col-2-menu {
  display: flex;
  flex-direction: column; }
  .sf-col-2-menu__item {
    display: block;
    font-size: 16px;
    line-height: 150%;
    color: #9D9EBF; }
    .sf-col-2-menu__item:not(:last-child) {
      margin-bottom: 13px; }
    .sf-col-2-menu__item--active, .sf-col-2-menu__item:hover {
      color: #ffffff; }

.sf-col-3 {
  margin-top: 17px;
  flex-grow: 1;
  display: flex;
  justify-content: center; }

.sf-col-3-inner {
  width: 223px;
  margin-left: 17%; }

.sf-a-btn {
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  font-size: 14px;
  line-height: 132%;
  padding: 10px 20px 8px 24px;
  display: flex;
  letter-spacing: -0.1px;
  justify-content: space-between; }
  .sf-a-btn .btn--with-right-arrow::after {
    margin-left: 10px; }
  .sf-a-btn--grey {
    color: #9D9EBF;
    border-color: #9D9EBF; }
    .sf-a-btn--grey::after {
      background-color: #9D9EBF;
      background-image: url(../img/arrow-right-blue.svg); }
  .sf-a-btn:not(:last-child) {
    margin-bottom: 16px; }

.sf-col-4 {
  margin-top: 17px;
  width: 272px;
  margin-right: 32px;
  margin-left: 68px; }

.sf-col-4-phone {
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  color: #FFFFFF;
  position: relative; }
  .sf-col-4-phone::before {
    position: absolute;
    left: -37px;
    top: 3px;
    width: 17px;
    height: 17px;
    content: '';
    display: block;
    background: url(../img/phone.svg) no-repeat center center;
    background-size: contain; }

.sf-col-4-work-time {
  font-size: 14px;
  line-height: 150%;
  color: #9D9EBF;
  margin-top: 2px; }

.sf-col-4-addr {
  font-size: 16px;
  line-height: 150%;
  margin-top: 17px;
  color: #9D9EBF; }

.sf-col-4-mail {
  display: block;
  font-size: 16px;
  line-height: 135%;
  margin-top: 17px;
  text-decoration-line: underline;
  color: #FFFFFF; }
  .sf-col-4-mail:hover {
    text-decoration: none; }

.sf-col-5 {
  padding: 23px 0 11px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between; }

.sf-col-5-social {
  transition-duration: .2s; }
  .sf-col-5-social img {
    height: 28px;
    width: auto; }
  .sf-col-5-social:hover {
    opacity: .7; }

@media screen and (max-width: 1000px) {
  .ts-flex {
    flex-direction: column;
    align-items: center;
    text-align: center; }
  .ts-pic {
    position: static; }
  .ts-flex__left {
    width: 100%; } }
